'use client';

import { PlaylistCard } from '@/components/playlist/playlist-card';
import { H1 } from '@/components/ui/typography';
import { PlaylistRecord } from '@/database-types';
import { getAllPlaylists } from '@/lib/api/playlist';
import { useEffect, useState } from 'react';

export default function Playlists() {
	const [playlists, setPlaylists] = useState<PlaylistRecord[]>([]);
	const [isLoading, setIsLoading] = useState(true);

	useEffect(() => {
		async function fetchPlaylists() {
			try {
				setIsLoading(true);
				const fetchedPlaylists = await getAllPlaylists();
				setPlaylists(fetchedPlaylists);
			} catch (error) {
				console.error('Error loading playlists:', error);
			} finally {
				setIsLoading(false);
			}
		}

		fetchPlaylists();
	}, []);

	return (
		<div className="flex flex-col items-start p-6 w-full">
			<span className="flex flex-col items-start gap-2">
				<H1 className="px-3">Your playlists</H1>

				{isLoading ? (
					<div className="px-3">Loading playlists...</div>
				) : (
					<div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4">
						{playlists.map((playlist: PlaylistRecord) => {
							return <PlaylistCard key={playlist.id} playlist={playlist} />;
						})}
					</div>
				)}
			</span>
		</div>
	);
}
